$ff: 'Open Sans', sans-serif;


*{
	margin: 0;
	padding: 0;
	-webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box; 
}

body{
  background-color: #ebf0f3;
  font-size: 14px;
  width: 100%;
  font-family: $ff;
  @media screen and (max-width: 865px) {
      font-size: 12px;        
  }
  @media screen and (max-width: 560px) {
      font-size: 10px;        
  }
}

.cl-mainContainer{
  width: 100%;
  margin-top: 50px;
  transition: 0.5;
  .cl-calculatorWrapper{
    position: relative;
    width: 60%;
    margin: 0 auto;
    background-color: #BFBFBF;
    @media screen and (max-width: 1220px) {
        width: 75%;
    }
    @media screen and (max-width: 980px) {
        width: 85%;
    }
    @media screen and (max-width: 768px) {
        width: 100%;
    }
    .cl-calculatorWrapper-title{
       text-align: center;
       padding: 30px 0;
       font-size: 2em;
       @media screen and (max-width: 768px) {
          font-size: 3em;
          width: 70%;
          text-align: center;
          margin: 0 auto;
       }
    }
    .cl-calculatorWrapper-inputs{
      position: relative;
      display: flex;
      flex-wrap: nowrap;
      justify-content: space-around;
      padding: 20px 20px 40px 20px;
      @media screen and (max-width: 768px) {
       flex-wrap: wrap;
      }
      .cl-calculatorWrapper-inputs__inputBlock{
        position: relative;
        @media screen and (max-width: 768px) {
         width: 100%;
        }
      }
      .cl-calculatorWrapper-inputs__nameOfInput{
        font-size: 0.8em;
        padding: 10px 0;
        @media screen and (max-width: 768px) {
         font-size: 1.2em;
         padding: 0 0 10px 0;
         margin-top: 15px;
       }
      }
      .cl-calculatorWrapper-inputs__input{
          min-height: 40px;
          border-width: 0;
          line-height: 40px;
          padding-left: 10px;
          -moz-appearance: textfield;
          -webkit-appearance:textfield;
          outline-offset: '-2px';
          @media screen and (max-width: 768px) {
           width: 100%;
           font-size: 2em;
          }
          &:focus{
            outline: none;
            outline-offset: -2px;

              -webkit-box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
              box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
            
          }
      }
      .cl-calculatorWrapper-inputs__input::-webkit-inner-spin-button { 
        display: none;
      }
      .cl-calculatorWrapper-inputs__button{
        height: 40px;
        align-self: flex-end;
        background-color: #106174;
        padding: 15px;
        color: white;
        border: none;
        border-radius: 5px;
        line-height: 0.7em;
        outline: none;
        border: none;
        cursor: pointer;
        &:hover{
          background: #20535f;
        }
        &:active{
          -webkit-box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.5);
          box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.5);
        }
        @media screen and (max-width: 768px) {
           width: 100%;
           margin-top: 40px;
           font-size: 1.5em;
        }
      }      
    }
    .cl-calculatorWrapper-result{
      display: none;
      margin: 0px auto;
      padding: 0 0 30px 0 ;
      width: 80%;
      text-align: center;
      .cl-calculatorWrapper-result__labelForResult{
        display: inline-block;
        padding-right: 20px;
        font-weight: bold;
        @media screen and (max-width: 768px) {
           font-size: 2em;
        }
      }
      .cl-calculatorWrapper-result__outPutResult{
        display: inline-block;
        padding:10px;
        background-color: #a8a9ab;
        color: white;
        font-size: 1.2em;
      }
    }
  }
}

.cl-cpanTooltip{
  background-color: rgba(73, 56, 56, 0.8);
  border-radius: 15px 15px 15px 0px;
  -webkit-box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.5);
          box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.5);
  color: #fff;
  margin-left: 20px;
  padding: 10px 3px ;
  text-align: center;
  position: absolute;
  text-decoration: none;
  width: 300px;
  z-index: 10;
  top: -15px;
  left: 0;
  @media screen and (max-width: 1050px) {
     width: 250px;
     font-size: 1em;
     margin-left: 5px;
  }
  @media screen and (max-width: 768px) {
     width: 300px;
     font-size: 1.5em;
     margin-left: 5px;
  }
}
.cl-worningWindow{
  display: none;
  position: absolute;
  width: 60%;
  text-align: center;
  -webkit-box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.5);
          box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.5);
          position: absolute;
  width: 60%;
  top: 50%;
  text-align: center;
  padding: 15px;
  margin: 0 auto;
  border-radius: 5px;
  background: #fcfcfcf5;
  left: 20%;
    .cl-worningMassage{
    display: inline-block;
    }
  .cl-closee{
    position: absolute;
      right: 10px;
      color: #6e6767;
      font-size: 30px;
      font-weight: 700;
      -webkit-transition: .3s;
      -o-transition: .3s;
      transition: .3s;
      z-index: 1000;
  }
  .cl-closee:hover,.cl-closee:focus{
    color: #bbb;
    text-decoration: none;
    cursor: pointer;
  }

}
